import React from 'react';

const Welcome = (props) => {
  const { name } = props;
  return (
    <div>
      {`Hello ${name}`}
    </div>
  );
}
const componentPropsTest = () => {
  console.log('component and Props');
  return (
    <div>
      <p>Component and Props</p>
      <Welcome name="Park Hansol" />
    </div>
  );
};

export default componentPropsTest;
